@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
  .toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #222222;
    height: 45px;
  }
}
@media (min-width: 769px) {
  .toolbar__dTB {
    display: none;
  }
}
